import React from "react";
import PropTypes from "prop-types";
// react plugin for creating charts
import ChartistGraph from "react-chartist";
import SweetAlert from "react-bootstrap-sweetalert";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";

// @material-ui/icons
import Money from "@material-ui/icons/AttachMoney";
import Search from "@material-ui/icons/Search";
import Timeline from "@material-ui/icons/Timeline";
import TrendingUp from "@material-ui/icons/TrendingUp";
import TrendingDown from "@material-ui/icons/TrendingDown";

// core components
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import Table from "components/Table/Table.jsx";
import Button from "components/CustomButtons/Button.jsx";
import Card from "components/Card/Card.jsx";
import CardHeader from "components/Card/CardHeader.jsx";
import CardIcon from "components/Card/CardIcon.jsx";
import CardBody from "components/Card/CardBody.jsx";
import Paper from '@material-ui/core/Paper';
import MenuItem from '@material-ui/core/MenuItem';


import TextField from '@material-ui/core/TextField';

import { getStockMonthly, getPredictions, getPrediction } from '../../services/stock-service'


import Downshift from 'downshift'

import {  
  applyStockData,
  yunsai
} from "variables/charts";

import {
  getSuggestions
} from "variables/suggestions";

import dashboardStyle from "assets/jss/material-dashboard-pro-react/views/dashboardStyle";

function renderSuggestion(suggestionProps) {
  const { suggestion, index, itemProps, highlightedIndex, selectedItem } = suggestionProps;
  const isHighlighted = highlightedIndex === index;
  const isSelected = (selectedItem || '').indexOf(suggestion.label) > -1;

  return (
    <MenuItem
      {...itemProps}
      key={suggestion.label}
      selected={isHighlighted}      
      component="div"
      style={{
        fontWeight: isSelected ? 500 : 400,
      }}
    >
      {suggestion.label}
    </MenuItem>
  );
}

renderSuggestion.propTypes = {
  highlightedIndex: PropTypes.oneOfType([PropTypes.oneOf([null]), PropTypes.number]).isRequired,
  index: PropTypes.number.isRequired,
  itemProps: PropTypes.object.isRequired,
  selectedItem: PropTypes.string.isRequired,
  suggestion: PropTypes.shape({
    label: PropTypes.string.isRequired,
  }).isRequired,
};

class Dashboard extends React.Component {
  state = {
    alert: null,
    value: 0,
    stockLineChart: applyStockData(yunsai),
    selectedStock: '',
    predictionUp: [],
    predictionDown: [],
    selected: {code: '600602', name: '云赛智联', probability: '0', trending: 1}
  };

  componentDidMount() {
    getPredictions().then(predictions => {
      this.setState({
        predictionUp: predictions[0], 
        predictionDown: predictions[1]
      })
    })

    // getPredictionUp().then(predictions => {
    //   this.setState({predictionUp: predictions})
    // })
  }

  renderInput(inputProps) {
    const { InputProps, classes, ref, ...other } = inputProps;
    const searchButton =
      classes.top +
      " " +
      classes.searchButton;

    // if(inputProps.inputProps && inputProps.inputProps.value && inputProps.inputProps.value.indexOf('-') > 0) {
    //   this.setState(
    //     {value: inputProps.inputProps.value}
    //   )
    // }
    return (
      <div>
      <GridContainer>
      
      <GridItem xs={10} md={5}>
      <TextField
        InputProps={{
          inputRef: ref,
          classes: {
            root: classes.inputRoot,
            input: classes.inputInput,
          },
          ...InputProps,
        }}
        {...other}
      />
      
      </GridItem>
      <GridItem xs={2} md={1}><Button
                    color="white"
                    aria-label="edit"
                    justIcon
                    round
                    className={searchButton}
                    onClick={() => this.handleSearch()}
                  >
                    <Search
                      className={classes.headerLinksSvg + " " + classes.searchIcon}
                    />
                  </Button></GridItem>
      </GridContainer>
      </div>
    );
  }
  handleChange = (value) => {
    this.setState({ selectedStock: value });
  };
  handleChangeIndex = index => {
    this.setState({ value: index });
  };
  hideAlert() {
    this.setState({
      alert: null
    });    
  };
  handleSearch() {
    if(!this.state.selectedStock || this.state.selectedStock.indexOf('-') <= 0) {
      this.showAlert('请输入正确的内容后检索。')
      return
    }

    let code = this.state.selectedStock.split('-')[0]

    getStockMonthly(code)
     .then(data => {
       this.setState ({
         stockLineChart: applyStockData(data)
       })
     })

    getPrediction(code) 
     .then(data => {
       this.setState({selected: data})
     })
  };
  showAlert(message) {
    this.setState({
        alert: (<SweetAlert
        style={{ display: "block", marginTop: "-100px" }}
        title={message}
        onConfirm={() => this.hideAlert()}
        onCancel={() => this.hideAlert()}
        confirmBtnCssClass={
          this.props.classes.alertButton
        }
      />
      )
    }
    );
  };
  
  render() {
    const { classes,rtlActive } = this.props;
    const searchButton =
      classes.top +
      " " +
      classes.searchButton;

    let upTableData = this.state.predictionUp.map(item => 
      [
        item.trending == 1 ? <TrendingUp color="error"/> : <TrendingDown color="action"/>,
        `${item.code}-${item.name}`,
        item.close,
        `${item.probability}%`,        
      ])

    let downTableData = this.state.predictionDown.map(item => 
        [
          item.trending == 1 ? <TrendingUp color="error"/> : <TrendingDown color="action"/>,
          `${item.code}-${item.name}`,
          item.close,
          `${item.probability}%`,        
        ])
    return (
      <div>            
        {this.state.alert}    
        <GridContainer>
          <GridItem xs={12}>
            <Card>
              <CardHeader color="success" icon>
                <CardIcon color="success">
                  <Money />
                </CardIcon>
                <h4 className={classes.cardIconTitle}>
                  今日股票推荐
                </h4>
              </CardHeader>
              <CardBody>
                <GridContainer justify="space-between">
                  <GridItem xs={12} sm={12} md={5}>
                    <Table
                      tableData={upTableData}
                    />
                  </GridItem>
                  <GridItem xs={12} sm={12} md={6}>
                  <Table
                      tableData={downTableData}
                    />
                  </GridItem>
                </GridContainer>
              </CardBody>
            </Card>
          </GridItem>
        </GridContainer>        
        <h3>股票详情</h3>
        <br />
        <GridContainer>
          <GridItem xs={12} sm={12} md={12}>
          <div>
          <Downshift id="downshift-simple"  onChange={this.handleChange}>
            {({
              getInputProps,
              getItemProps,
              getLabelProps,
              getMenuProps,
              highlightedIndex,
              inputValue,
              isOpen,
              selectedItem,
            }) => {
              const { onBlur, onFocus, ...inputProps } = getInputProps({
                placeholder: '股票代码/股票名称',
              });

              return (
                <div className={classes.container}>
                  {this.renderInput({
                    fullWidth: true,
                    classes,                
                    InputLabelProps: getLabelProps({ shrink: true }),
                    InputProps: { onBlur, onFocus },
                    inputProps,
                  })}

                  <div {...getMenuProps()}>
                    {isOpen ? (
                      <Paper className={classes.paper} square>
                        {getSuggestions(inputValue).map((suggestion, index) =>
                          renderSuggestion({
                            suggestion,
                            index,
                            itemProps: getItemProps({ item: suggestion.label }),
                            highlightedIndex,
                            selectedItem,
                          }),
                        )}
                      </Paper>
                    ) : null}
                  </div>
                </div>
              );
            }}
          </Downshift>
          </div>
          </GridItem>
          <GridItem xs={12} sm={12} md={12}>
            <Card>
              <CardHeader color="info" icon>
                <CardIcon color="info">              
                  {this.state.selected.trending == 1 ? <TrendingUp /> : <TrendingDown /> }
                </CardIcon>
                  <h4 className={classes.cardIconTitle}>
                    {this.state.selected.name}[{this.state.selected.code}] - {this.state.selected.probability}%
                  </h4>
              </CardHeader>
              <CardBody>
                <ChartistGraph
                  data={this.state.stockLineChart.data}
                  type="Line"
                  options={this.state.stockLineChart.options}
                  listener={this.state.stockLineChart.animation}
                />
              </CardBody>
            </Card>
          </GridItem>
        </GridContainer>
      </div>
    );
  }
}

Dashboard.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(dashboardStyle)(Dashboard);
