const applyStockData = (stockData) => {  
  const prices = stockData.map(item => item[2].toFixed(2)).reverse();
  const max = Math.ceil(Math.max.apply(null, prices));
  // const months = yunsai.map(item => item[1].substring(4, 8));
  const months = stockData.map(item => item[1]).reverse();  
  stockLineChart.options.high = max;
  stockLineChart.data.labels = months;
  stockLineChart.data.series = [prices];

  return stockLineChart;
}

module.exports = {
  applyStockData,
  yunsai
};
