import 'whatwg-fetch'

const baseUrl = "http://localhost:8080"

function checkStatus(response) {
  if (response.status >= 200 && response.status < 300) {
    return response
  } else {
    var error = new Error(response.statusText)
    error.response = response
    throw error
  }
}

function parseJSON(response) {
  return response.json()
}

const getStockMonthly = (code) => {
  return fetch(`${baseUrl}/stocks/${code}`)
  .then(checkStatus)
  .then(parseJSON)
  .then(resp => {
    let data = resp.data.data.items
    return Promise.resolve(data)
  }) 
}

const getPredictionUp = () => {
  return fetch(`${baseUrl}/predictions/up`)  
  .then(checkStatus)
  .then(parseJSON)
  .then(resp => {
    let data = resp.data
    return Promise.resolve(data)
  }) 
}

const getPredictionDown = () => {
  return fetch(`${baseUrl}/predictions/down`)  
  .then(checkStatus)
  .then(parseJSON)
  .then(resp => {
    let data = resp.data
    return Promise.resolve(data)
  }) 
}

const getPrediction = (code) => {
  return fetch(`${baseUrl}/predictions/${code}`)  
  .then(checkStatus)
  .then(parseJSON)
  .then(resp => {
    let data = resp.data
    return Promise.resolve(data)
  }) 
}

const getPredictions = () => {
  return Promise.all([getPredictionUp(), getPredictionDown()])
}

export { getStockMonthly, getPredictions, getPrediction }